import { Form } from "react-bootstrap"
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let box = styled.div`
  padding : 20px;  
`;

let 제목 = styled.div`
  font-size : 25px;
  color : ${ props => props.색상 }
`;

function Detail(props){ 

    let [alert,alert변경] = useState(true);
    let [입력값,입력값변경] = useState('');
    
    useEffect(()=>{
        let timer = setTimeout(()=>{ alert변경(false) },2000);
        return ()=>{clearTimeout(timer)}
    },[]);

    let { id } = useParams();
    let findingItem = props.item.find(function(F_item){
        return F_item.id == id
    })
    let history = useHistory();

    function InfoAlt(){
      var 재고쓰 = [...props.재고];

      재고쓰[0]-=1;

      props.재고Alt(재고쓰);
    }

    return(
      <div className="container">
        <box>
          <제목 className="red">
            상세페이지
          </제목>
        </box>

        {/* <input onChange={(e)=>{ 입력값변경(e.target.value) }}/>
        {입력값} */}


        {
          alert?
        <div className="my-alert">
          <p>재고가 얼마 남지 않았습니다</p>
        </div>
        : null
        }
            <div className="row">
              <div className="col-md-6">
                <img src={'https://codingapple1.github.io/shop/shoes'+ findingItem.id +'.jpg'} width="100%" />
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{findingItem.title}</h4>
                <p>{findingItem.content}</p>
                <p>{findingItem.price}원</p>

                <Info 재고={props.재고}></Info>

                <button className="btn btn-danger" onClick={()=>{InfoAlt()}}>주문하기</button>
                <button className="btn btn-danger" onClick={()=>{
                    history.goBack();
                }}>뒤로가기</button>
              </div>
            </div>
          </div>
    )
  }


  function Info(props){
    return(
      <p>재고 : {props.재고[0]}</p>
    )
  }


  export default Detail;